.App {
  display: inline-block;
  min-height: 100vh;
  background-image: linear-gradient(-90deg, #ff00a1, #ddbd04);
}

.fade-enter {
  opacity: 0.01;
}

.fade-enter.fade-enter-active {
  opacity: 1;
  transition: opacity 300ms ease;
}

.fade-exit {
  opacity: 1;
}

.fade-exit.fade-exit-active {
  opacity: 0.01;
  transition: opacity 300ms ease;
}


